<template>
    <div class="container">
        <ul :style="'transform: translateX(-' + offset/9 * 100+'%)'" class="carouse-items"><li><img :src="'./img/banner7.jpg'" alt=""></li><li v-for="index in 7" :key="index "><img :src="'./img/banner' + index+'.jpg'" alt=""></li><li><img :src="'./img/banner1.jpg'" alt=""></li></ul>
        <div class="indi"><ul class="carouse-indicators"><li class="carouse-indicator" :class="{active:num - 1===now}" v-for="num in 7" :key= num></li></ul>
        </div>
        <div class="placeholder"></div>
        <div class="bimgs"><img :src="'./img/left.jpg'" alt=""><img :src="'./img/mid.jpg'" alt=""><img :src="'./img/right.jpg'" alt=""></div>
    </div>
</template>

<script>
    export default {
        components:{},
        props:{},
        data(){
            return {
                inter:0,
                now:0,
                docWidth:0,
                offset: 1,
            }
        },
        mounted(){

            
            var ul = this.$el.getElementsByClassName("carouse-items")[0];
            this.docWidth = document.documentElement.offsetWidth;
            var startx;
            var dir;
            this.inter = setInterval(()=>{
                ul.style.transition="0.5s all linear";          
                this.offset = (Math.floor(this.offset) + 1)%9; 
                this.now = (this.now + 1) % 7;
            },3000)
            ul.addEventListener("transitionend",()=>{
                if(Math.floor(this.offset) === 8){
                    ul.style.transition="none";
                    this.offset = 1;
                }
                else if(Math.floor(this.offset) === 0){
                    ul.style.transition = "none";
                    this.offset = 7;
                }
            })
            ul.addEventListener("touchstart", (e)=>{
                this.docWidth = document.documentElement.offsetWidth;
                ul.style.transition = 'none';
                startx = e.touches[0].clientX;
                clearInterval(this.inter);})
            ul.addEventListener("touchmove",(e)=>{
                
                let diff = -e.touches[0].clientX + startx;
                startx = e.touches[0].clientX;
                this.offset += diff/this.docWidth;
                console.log(this.offset/9 * 100);
                if(diff < 0)
                    dir = -1;
                else if(diff > 0)
                    dir = 1;
                else
                    dir = 0;
            })
            ul.addEventListener('touchend', ()=>{
                ul.style.transition = "0.5s all linear";    
                if(dir<=0)
                    {this.offset = Math.floor(this.offset);this.now = (this.now + 6) % 7;}
                else if(dir > 0)
                    {this.offset = Math.floor(this.offset) + 1;this.now = (this.now + 1) % 7}
                this.inter = setInterval(()=>{
                ul.style.transition="0.5s all linear";          
                this.offset = (Math.floor(this.offset) + 1)%9;
                this.now = (this.now + 1) % 7;
            },3000);})
           
        },
    }
</script>

<style scoped>
    .container{
        position: relative;
        overflow: hidden;
    }
    .carouse-indicator{
        width: 8px;
        height: 8px;
        border-radius: 50%;
        display: inline-block;
        background-color: #fff;
        transition: 0.5s width linear;
        margin: 5px;
    }
    .carouse-indicator.active{
        transition: 0.5s width linear;
        width: 16px;
        background-color: #ff3c25;
        border-radius: 5px;
    }
    .carouse-items{
        position: relative;
        width: 900%;
        transition: 0.5s all linear;
    }
    li{
        display: inline-block;
    }
    img{
        width:100%;
    }
    .carouse-items li{
        width: 11.1111111%;
    }
    .carouse-items .cur{
        opacity: 1;
    }
    .indi{
        position: absolute;
        left: 50%;
        bottom:15%;
        z-index: 100;
    }
    .carouse-indicators{
        position: relative;
        margin: auto;
        right: 50%;
    }
    .placeholder{
        padding-top: 20%;
    }
.bimgs{
        position: absolute;
        top: 60%;
    }
.bimgs img{
        width: 33.33%;
    }
</style>