<template>
    <div>
        <h1>懂你想要</h1>
        <div class="items">
            <ul class="left">
                <li v-for="(item, index) in leftItems" :key="index">
                    <img :src="'./img/itme'+item.index +'.jpg'" alt="">
                    <p>{{item.describtion}}</p>
                    <p class="price">{{item.price}}</p>
                </li>
            </ul>
            <ul class="right">
                <li v-for="(item, index) in rightItems" :key="index">
                    <img :src="'./img/itme'+item.index +'.jpg'" alt="">
                    <p>{{item.describtion}}</p>
                    <p class="price">{{item.price}}</p>
                </li>
            </ul>
        </div>
        <div  class="loading" v-if="showload">正在加载请稍后……</div>
        <div  class="done" v-if="showdone">不要再拉了，下面已经没有了</div>
    </div>
</template>

<script>
    export default {
        components:{},
        props:{},
        mounted(){
            window.addEventListener('scroll', ()=>{
                let curTop = window.pageYOffset;//获取当前的top值，与之前的比较判断是向上滑还是向下
                
            
                if(window.pageYOffset >= document.documentElement.scrollHeight *2 / 3 && this.isavailable && curTop > this.top)
                {
                    this.isavailable = false;
                    const getData = new Promise((resolve, reject)=>{//获取数据
                        let xhr = new XMLHttpRequest();
                        xhr.responseType = 'json';
                        xhr.open('get','./data/items_infor' + this.cur + '.json');
                        xhr.onreadystatechange = ()=>{
                            if(xhr.readyState ===4)
                            {
                                if(xhr.status === 200){
                                    resolve(xhr.response);
                                }
                                else
                                    reject();
                            }
                            else{
                                this.showload = true;
                            }
                    }
                    xhr.send();
                    }).then((data)=>{
                        let len = data.length;
                        for(let i = 0;i<len;i++){
                            if((i+1) %2 == 0)
                                this.rightItems.push(data[i]);
                            else
                                this.leftItems.push(data[i]);
                        }
                        this.showload = false;this.showdone = false;this.cur++;this.isavailable = true;
                    }).catch(()=>{
                        this.showload = false;
                        this.showdone = true;
                    });
                    
                }
                this.top = curTop;
            })
        },
        data(){
            return {
                cur:0,
                showload:false,
                showdone:false,
                leftItems:[],
                rightItems:[],
                isavailable: true,
                top:0
            }
        },
    }
</script>

<style scoped>
    ul{
        list-style-type: none;
        width: 50%;
    }
    ul.left{
        float: left;
    }
    ul.right{
        float: right;
    }
    li{
        padding: 1rem;
        background: white;
    }
    img{
        width: 100%;
    }
    .items{
        overflow: hidden;
        background: white;
    }
    .loading, .done{
        color: #bdbdbd;
        text-align: center;
        font-size: 2rem;
        margin: 1rem 0;
    }
    p{
        padding: 0.4rem 0;
    }
    .price{
        color: #ff3c25;
        font-size: 1.6rem;
    }
</style>