<template>
    <div class="container">
        <div class="header"><h2>超级单品</h2> <div class="time"><span>{{hour}}</span>：<span>{{minute}}</span>：<span>{{second}}</span></div>
        <span class="red">爆款超值购 >></span></div>
        <ul>
            <li v-for="(product, index) in productInfors" :key="index">
                <img :src="'./img/img' + (index) +'.jpg'" alt="">
                <p>{{product.name}}</p>
                <p class="price"><span class="cur-price">{{product.newPrice}}</span><span class="old-price">{{product.oldPrice}}</span></p>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        components:{},
        props:{},
        data(){
            return {
                productInfors:[],
                time:10800000,
                inter:0
            }
        },
        mounted(){
            this.inter = setInterval(()=>{this.time -= 1000;}, 1000);
        },
        created(){
            let _this = this;
            const getProduct = new Promise(function(resolve,reject){
                let xhr = new XMLHttpRequest();
                xhr.onreadystatechange = function(){
                    if(xhr.readyState === 4)
                        if(xhr.status === 200)
                            resolve(xhr.response);
                        else
                            reject(new Error(xhr.statusText))
                }
                xhr.open('get','./productInfor.json',);
                xhr.responseType = 'json';
                xhr.send();
            }).then(function(data){
                _this.productInfors = data;
            }).catch(function(error){
                console.log(error);
            })
        },//ajax请求商品信息
        computed:{
            hour(){
                
                return '0' + new Date(this.time).getUTCHours();
            },
            minute(){
                let m = new Date(this.time).getUTCMinutes();
                if(m < 10)
                    return '0' + m;
                else
                    return m;
            },
            second(){
                let s = new Date(this.time).getUTCSeconds();
                if(s < 10)
                    return '0' + s;
                else 
                    return s;
            }
        },
        watch:{
            time(){
                if(this.time === 0)
                    clearInterval(this.inter)
            }
        }
    }
</script>

<style scoped>
    .container{
        overflow: scroll;
    }
    ul{
        white-space: nowrap;
        width: 200%;
        clear: both;
    }
    li{
        display: inline-block;
        width: 12.5%;
        padding: 1rem;
        text-align: center;
    }
    img{
        width: 100%;
    }
    .cur-price{
        color: #ff3c25;
        font-size: 1.2em;
    }
    .old-price{
        color: #999;
        text-decoration: line-through;

    }
    h2{
        float: left;
    }
    .time{
        float: left;
        margin-left: 1em;

    }
    .time span{
        color: white;
        background: #6b6b6b;
        padding: 0.1em 0.2em;
        font-size: 1.3em;
        border-radius: 0.4em;
    }
    .red{
        float: right;
        color: #ff3c25;
        padding-right: 1rem;    
    }
    
</style>