<template>
    <div class="container">
        <div class="header"><a href="">
            <img class="header-img" :src="'./img/bg' + this.title+'.png'" alt="">
            <h2>{{title}}</h2>
            <img :src="'./img/' + title +'0.jpg'" alt="">
        </a>
        <a href="">
            <img :src="'./img/' +title + '1.jpg'" alt="">
        </a></div>
        <div class="items">
        <ul>
            <li v-for="(catalog, index) in catalogs.products" :key='index'>
                <img :src="'./img/' +catalog+'.jpg'" alt="">
                <p>{{catalog}}</p>
            </li>
        </ul></div>
    </div>
</template>

<script>
    export default {
        components:{},
        props:{
            title:{
                type:String,
                required:true
            }
        },
        data(){
            return {
                catalogs:[],
                
            }
        },
        created(){
            let xhr = new XMLHttpRequest();
            const ajax = new Promise((resolve, reject)=>{
                xhr.open('get','./data/'+this.title+'.json');
                xhr.responseType ='json';
                xhr.onreadystatechange = ()=>{
                    if(xhr.readyState === 4)
                    {
                        if(xhr.status === 200)
                            resolve(xhr.response);
                        else
                            reject("can't get" + this.title+".json");
                    }
                }
                xhr.send();
            }).then((data)=>{
                this.catalogs = data;
            }).catch((e)=>{console.log(e);})
        }
    }
</script>

<style scoped>
    .container{
        margin-top: 1rem;
    }
    .header img{
        width: 50%;
    }
    .header a{
        position: relative;
    }
    .header h2{
        position:absolute;
        color: white;
        width: 90px;
        height: 33px;
        text-align: center;
        font-size: 1.6rem;
        background-size: 90px;
        background-repeat: no-repeat;
        line-height: 33px;
    }
    .header .header-img{
        position: absolute;
        width:90px;
        left: -5px;
    }
    .items{
        overflow: scroll;
    }
    ul{
        width: 200%;
       
    }
    li{
        display: inline-block;
        width: 12.5%;
        text-align: center;
    }
    li img{
        width: 100%;
    }
    p{
        margin: 0.5rem 0 ;
    }
</style>